
import React from "react";

// Controladores

const status = {
    found: {
        backgroundColor: 'node',
        color: 'white',
        borderBottom: '6px solid #C4C4C4'
    },
    select: {
        backgroundColor: 'none',
        color: 'white',
        borderBottom: '6px solid #C0FC97'
    },
    hit: {
        backgroundColor: '#C0FC97',
        color: 'white'
    }
}


export default class Gaps extends React.Component {

    // eslint-disable-next-line no-useless-constructor
    constructor(props) {

        super(props);

    }

    ControleGap(index) {
        this.props.ControleGap(index)
    }

    renderGap(item, index) {
        return (
            <div className="flex-fill w-100 h-gap m-2 text-center"
                style={index === this.props.StateGap.gap[this.props.ControleJogo.state.focus] ? status.select : status.found}
                onClick={() => this.ControleGap(index)}
                key={index}

            >
                {this.props.ControleJogo.state.statusWords[this.props.ControleJogo.state.focus][index]}
            </div>
        );
    }


    renderGaps(item) {
        var gaps = [];
        for (let index = 0; index < item.letras.length; index++) {
            gaps.push(this.renderGap(item, index));
        }
        return React.createElement('div', { className: "d-flex w-100" }, gaps);
    }


    render() {

        return (
            <div className="d-flex w-100 justify-content-center">
                <div className="Gaps flex px-md-1 w-100">
                    {/* <h5> Lacunas </h5> */}
                    {this.renderGaps(this.props.ControleJogo.Construtor.principal[this.props.ControleJogo.state.focus])}
                </div>
            </div>

        );
    }
}