import React from "react";

// Controladores

export default class Buttons extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: 0
        }
    }

    // Linked Funcitons
    deleteLetter(index) {
        if (index !== null && this.props.ControleJogo.state.hitWord[index] === false) {
            this.props.ControleGap(0);
            this.props.ControleJogo.deleteLetter(index, this.props.ControleJogo.Construtor.principal[
                index
            ].posicao)
            this.props.update();
        }
    }

    selectLetter(item, letter) {
        this.props.ControleJogo.selectLetter(
            item.posicao[
            this.props.StateGap.gap[this.props.ControleJogo.state.focus]
            ],
            this.props.StateGap.gap[this.props.ControleJogo.state.focus],
            letter
        );
        this.ControleGap();
        this.props.update();
    }

    ControleGap() {
        this.props.ControleGap(
            this.props.StateGap.gap[this.props.ControleJogo.state.focus] + 1
        );
    }

    // End Linked Funcitons

    renderButton(item, index) {
        return (
            <div
                className="flex ps-2 pt-2 pb-0 pe-0"
                style={this.props.ControleJogo.ControleModo.widthTeclas}
                key={index}
            >
                <button
                    type="button"
                    key={index + 100}
                    onClick={() => this.selectLetter(item, item.teclas[index])}
                    className="button-teclas bg-color1 w-100 arround-6 text-white1 text-center"
                >
                    {item.teclas[index]}
                </button>
            </div>
        );
    }

    renderButtons(item) {
        var buttons = [];
        for (let index = 0; index < item.teclas.length; index++) {
            buttons.push(this.renderButton(item, index));
        }
        return React.createElement(
            "div",
            { className: "d-flex  w-100 flex-wrap" },
            buttons
        );
    }

    render() {
        return (
          <div className="Buttons d-flex container align-items-stretch px-md-1">
            <div className="flex  pt-2 position-relative  align-self-stretch">
              <button            
                onClick={() => this.deleteLetter(this.props.ControleJogo.state.focus)}
    
                type="button"
                className="button-teclas bg-color1 w-100 h-100 arround-6 text-white1 text-center"
              >
                <i className="material-icons md-light text-white1 md-24">
                backspace
                </i>
              </button>
            </div>
            {this.renderButtons(
              this.props.ControleJogo.Construtor.principal[
                this.props.ControleJogo.state.focus
              ]
            )}
            <div className="flex px-2 pt-2 position-relative  align-self-stretch">
              <span className="badge position-absolute top-0 end-0 rounded-circle  z-index-badge">
                {this.props.ControleJogo.state.tips}
              </span>
              <button
                data-bs-toggle="modal"
                data-bs-target="#staticTips"
                type="button"
                className="button-teclas bg-color1 w-100 h-100 arround-6 text-white1 text-center"
              >
                <i className="material-icons md-light text-white1 md-24">
                  help_outline
                </i>
                Dica
              </button>
            </div>
          </div>
        );
      }
    }