import React from "react";

// ViewsComponents
import Teclado from "./ViewComponentsJogo/teclado";
import Tabuleiro from "./ViewComponentsJogo/tabuleiro";
import Lista from "./ViewComponentsJogo/lista";
import ModalFull from "./ViewComponents/ViewComponentsNav/ModalFull";
import Vitoria from "./ViewComponentsJogo/SubViewComponentsJogo/vitoria";

export default class ViewJogo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      spinner: false,
      vitoria: true,
    };

    // Vinculo entre que força atualização entre os filhos, este componente, e o props.Jogo
    this.update = this.update.bind(this);

    //Funções próprias - caso necessario
    this.next = this.next.bind(this);

    
  }

  

  

  componentDidMount() {
    
  }

  next() {
    this.props.Jogo.next();
    this.update();
  }

  update() {
    this.forceUpdate();
  }

  render() {
    if (!this.props.Jogo.state.hitWord.includes(false))
    // if(true)
      return <Vitoria ControleJogo={this.props.Jogo} Serve={this.props.Serve}  />;
    else
    return (
      <div className="d-flex w-100 flex-row container-md min-vh-100 bg align-items-jogo  p-0">
        <div className="flex-fill d-flex flex-column align-items-center w-100 viewjogo  arround-12 ">         
          <Tabuleiro ControleJogo={this.props.Jogo} update={this.update}  Serve={this.props.Serve} />
      
          <Teclado ControleJogo={this.props.Jogo} update={this.update} />
        </div>
        <div id="lista-posicao" className="flex-fill bg-color2 viewjogo arround-tr-12 arround-br-12 none-md lista">
          <Lista ControleJogo={this.props.Jogo} update={this.update} />
        </div>

        <ModalFull vinculo="modal-lista">
          <Lista ControleJogo={this.props.Jogo} update={this.update} />
        </ModalFull>

        <i
          className="position-absolute  d-md-none icon-list end-0 material-icons-outlined md-gray md-36  mx-3 my-md-3"
          data-bs-toggle="modal"
          data-bs-target="#modal-lista"
        >
          view_sidebar
        </i>
        
      </div>
    );
  }
}
